.opener-header {
    text-align: center;
    font-family: 'Libre Baskerville';
}
.opener-para {
    text-align: justify;
    font-family: 'Libre Baskerville';
}

.opener-button {
    background-color: #000000;
    border-radius: 10px;
    position: absolute;
    margin: 0;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.opener {
    max-width: 80%;
    align-content: center;
    margin: auto;
}